<template>
  <div id="business" v-cloak>
    <headers></headers>
    <div class="business-main">
      <h1>
        <span class="title">企业管理</span>
        <el-button size="small">刷新</el-button>
        <el-button size="small" type="primary">新增</el-button>
      </h1>
      <el-form :model="businessForm" class="businessForm">
        <el-col :span="3" :lg="3" :md="3" :sm="3" :xs="8">
        <el-form-item>
            <el-select placeholder="所有状态" v-model="businessForm.status" size="small">
              <el-option v-for="item in status" :value="item.value" :label="item.label"></el-option>
            </el-select>
        </el-form-item>
        </el-col>
        <el-col :span="3" :lg="3" :md="3" :sm="3" :xs="8">
        <el-form-item>
            <el-select placeholder="所有类型" v-model="businessForm.type" size="small">
              <el-option v-for="item in type" :value="item.value" :label="item.label"></el-option>
            </el-select>
        </el-form-item>
        </el-col>
        <el-col :span="3" :lg="3" :md="3" :sm="3" :xs="8">
        <el-form-item>
            <el-select placeholder="所有地区" v-model="businessForm.area" size="small">
              <el-option v-for="item in area" :value="item.value" :label="item.label"></el-option>
            </el-select>
        </el-form-item>
        </el-col>
        <el-col :span="8" :lg="8" :md="8" :sm="8" :xs="24">
        <el-form-item>
            <el-input placeholder="请输入企业名称" v-model="businessForm.search" class="businessSearch" size="small"></el-input>
            <el-button type="primary" size="small" class="businessSearchBtn">搜索</el-button>
        </el-form-item>
        </el-col>
      </el-form>
      <el-table :data="businessTab" class="businessTab" @row-click="businessDetail" :row-class-name="businessTabClass">
        <el-table-column v-for="item in businessTit" :prop="item.prop" :width="item.width" :min-width="item.minWidth" :type="item.type" :label="item.label"></el-table-column>
        <!--<el-table-column prop="operate" label="操作">-->
          <!--<template scope="scope">-->
            <!--<el-button size="small">查看详情</el-button>-->
          <!--</template>-->
        <!--</el-table-column>-->
      </el-table>
      <div class="businessPagination">
        <el-pagination :current-page="businessPage.current"
                       :size="businessPage.size"
                       :total="businessPage.total"
                       @current-change="businessCurrentChange"
                       @size-change="businessSizeChange"
                       layout="total,prev,pager,next,jumper"
                       class="businessPage" :small="small">
        </el-pagination>
        <div class="businessPageInfo" v-show="businessPageInfo">
          <span>显示第<b> 1 </b>到<b> 10 </b>条数据</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import headers from '@/components/header.vue'
  export default{
    name: 'business',
    data(){
      return{
        loader: true,
        businessPageInfo: true,
        small: false,
        businessForm: {
          status: '',
          type: '',
          area: '',
          search: ''
        },
        businessPage: {
          current: 1,
          size: 10,
          total: 20
        },
        businessSearch: '',
        status: [
          {
              label: '在线',
            value: 'online'
          },
          {
            label: '离线',
            value: 'offline'
          }
        ],
        type: [
          {
            label: '租赁',
            value: 'rent'
          }
        ],
        area: [
          {
            label: '广州',
            value: 'guangzhou'
          },
          {
            label: '深圳',
            value: 'shenzhen'
          }
        ],
        businessTit: [
          {
            prop: 'status',
            width: 100,
            label: '状态'
          },
          {
            prop: 'name',
            minWidth: 300,
            label: '企业名称'
          },
          {
            prop: 'contactor',
            width: '',
            label: '联系人'
          },
          {
            prop: 'address',
            minWidth: 280,
            label: '企业地址'
          },
          {
            prop: 'created',
            width: '',
            label: '创建日期'
          }
        ],
        businessTab: [
          {
            status: '离线',
            name: '广东小哈科技股份有限公司',
            contactor: '帅哥',
            address: '广州市车陂',
            created: '2017-05-26'
          },
          {
            status: '在线',
            name: '加贝尔',
            contactor: '美女',
            address: '广州市番禺',
            created: '2017-05-29'
          }
        ]
      }
    },
    components: {
        headers
    },
    created(){
      this.loading()
      this.$nextTick(() => {
        if (screen.width <= 1199) {
          this.tableTit()
          this.businessPageInfo = false
          this.small = true
        }else{
          this.businessPageInfo = true
          this.small = false
        }
      })
    },
    methods: {
      businessDetail(row){
        this.$router.push({
          path: '/business/detail'
        })
      },
      businessCurrentChange(val){
        const self = this
        self.businessPage.current = val
      },
      businessSizeChange(val){
        const self = this
        self.businessPage.size = val
      },
      businessTabClass(row, column){
        return 'businessTabClass'
      },
      loading(){
        setTimeout(() => {
          this.loader = false
        }, 1000)
      },
      tableTit(){
        let body = document.getElementsByClassName('businessTabClass')
        for(let i = 0;i<body.length;i++){
            console.log(i)
          let cell = body[i].getElementsByClassName('cell')
          for(let j = 0;j<cell.length;j++){
            cell[j].innerHTML += '<div class="cellTit">' + this.businessTit[j].label + '</div>'
          }
        }
      }
    }
//    created(){
//        console.log(this.$router.beforeHooks)
//    }
  }
</script>



